        doc_from1: ['how are glacier caves formed?', 'How are the directions of the velocity and force vectors related in a circular motion', 'how did apollo creed die', 'how long is the term for federal judges', 'how a beretta model 21 pistols magazines works', 'how a vul works D9 Variable universal life insurance', 'how an outdoor wood boiler works', 'how big did girl scout cookie boxes used to be', 'how big is the purdue greek system', 'how big do sebaceous cysts get'],
        doc_from2: ['A glacier cave is a cave formed within the ice of a glacier .', 'In physics , circular motion is a movement of an object along the circumference of a circle or rotation along a circular path.', 'Urban legend states that Apollo Creed\'s name is a wordplay on the Apostles\' Creed , a statement of belief used in Christian churches.', 'Every judge appointed to such a court may be categorized as a federal judge; such positions include the Chief Justice and Associate Justices of the Supreme Court, Circuit Judges of the courts of appeals, and district judges of the United States district courts .', 'The Beretta 21A Bobcat is a small pocket-sized semi-automatic pistol designed by Beretta in Italy.', 'Variable Universal Life Insurance (often shortened to VUL) is a type of life insurance that builds a cash value.', 'The outdoor wood boiler is a variant of the classic wood stove adapted for set-up outdoors while still transferring the heat to interior buildings.', 'There are also unit incentives if the unit as a whole does well.', 'Purdue was founded on May 6, 1869, as a land-grant university when the Indiana General Assembly , taking advantage of the Morrill Act , accepted a donation of land and money from Lafayette businessman John Purdue to establish a college of science, technology, and agriculture in his name.', 'n practice, however, the terms are often used interchangeably..']
