      //发送第2类请求并定时进行轮询
      var myself = this;
      //3s轮询一次后端，要来preprocessing过程最新的控制台输出
      var preprocess_timer = setInterval(getPreprocessData, 3000);
      //0.6秒刷新一次页面，把最新的preprocessing控制台信息显示出来
      var setPreprocessTimer = setInterval(setPreprocessData, 600);
